import { Component, Injector, OnInit } from '@angular/core';
import { CookieService } from 'ngx-cookie-service';
import { BaseJoinAction } from '../class/base-join-actions';
@Component({
  selector: 'di-component-name-here',
  templateUrl: './dm-dashboard.component.html',
  styleUrls: ['./dm-dashboard.component.css']
})
export class DmDashboardComponent extends BaseJoinAction implements OnInit {

  constructor(
    injector : Injector,
    private cookie:CookieService
  ) {
    super(injector);
   }

  ngOnInit(): void {
    this._DashBoard();
  }

  _DashBoard(){
    if(this.cookie.get('current_module') == '2') this._router.navigateByUrl('/dm_dashboard');
    else if(this.cookie.get('current_module') == '') window.location.href = this._fhs._logout();
    else  this._router.navigateByUrl('/e_raseed_dashboard');
  }

}
